<template>
  <div class="login">
  <img src="../assets/logo.png" alt="" width="60%">
  <InputGroup type="text" hint="请输入手机号码" :btnTitle="btnTitle" @input="rephone" :error="error.phone" :value="value" @getcode="getVerifyCode" :disabled='disabled'></InputGroup>	
	<InputGroup type="text" hint="请输入验证码" v-model="verifyCode" :error="error.code"></InputGroup>	
  	<div class="login_des">
      <p>
        新用户登录即自动注册，表示已同意
        <span>《用户服务协议》</span>
      </p>
    </div>
  <button class="btn1" @click="handleLogin">登录</button>
  </div>
</template>

<script>
import InputGroup from "../components/InputGroup.vue"
export default {
  name: 'login',
  data:function () {
  	return {
  		error:{},
  		value:'',
  		verifyCode:'',
  		btnTitle:'获取验证码',
  		disabled:false
  	}
  },
  components:{
  	InputGroup
  },
  methods:{
  	rephone(value){
  		if(value == ''){
  			this.error = {phone:"手机号码不能为空"};	
  			this.value = value;
  			return false;	
  		}else if(!(/^1[34578]\d{9}$/.test(value))){
  			this.error = {phone:"手机号码输入错误"};
  			this.value = value;
  			return false;	
  			// console.log(this.error.phone); 			
  		}else{
  			this.error = {};
  			this.value = value;
  			return true;	
  		}
  	},
  	getVerifyCode(){
  		if(this.rephone(this.value)){
  			console.log(this.value);
  			this.validateBtn();
  			this.$axios.post(`/api/posts/sms_send`,{
  				tpl_id:'162864',
  				key:'f42643cf9ddc70a497524eb54c5d9382',
  				phone:this.value
  			})
  			.then(res=>{
  				console.log(res);
  			})
  		}
  	},
  	handleLogin(){
  		// console.log(this.value,this.verifyCode);
  		if(this.value&&this.verifyCode){
  			this.error = {};
  			this.$axios.post("/api/posts/sms_back",{
  				phone:this.value,
  				code:this.verifyCode
  			}).then(res=>{
  				localStorage.setItem("user",true);
  				this.$router.push('/');
  			}).catch(err=>{
  				this.error = {
  					code:err.response.data.msg
  				}
  				localStorage.setItem("user",true);
  				this.$router.push('/');
  			})
  		}else{
  			alert("请完善信息")
  		};
  	},
  	validateBtn(){
  		let m = 60;
  		let $this  =this;
  		let time = setInterval(function(){
  			if(m == 0){
  				clearInterval(time);
  				$this.btnTitle = '获取验证码';
  				$this.disabled = false;
  				m = 60;
  			}else{
  				$this.btnTitle = `${m}后重新发送`;
  				$this.disabled = true;
  				m--;
  			}
  		},1000);
  	}
  }
}
</script>


<style scoped>
.login{
	text-align: center;
	padding: 10px;
}
img{
	max-width: 500px;
}
.btn1{
	width: 100%;
	border:none;
	max-width: 600px;
	height: 35px;
	border-radius: 5px;
	margin-top: 25px;
	background-color: #1f8a39;
	color: #fff;
}
.login_des{
	margin: 0 auto;
	max-width: 600px;
}
.login_des p{
	margin-top: 10px;
	font-size: 14px;
	text-align: left;
	color: #8b8b8b;
}
.login_des span{
	color: #00f;
}
</style>
