<template>
  <div id="app">
      <!-- <router-link to="/login">Home</router-link> | -->
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  methods:{
  	getlocation(){
  		let $this = this;
  		AMap.plugin('AMap.Geolocation', function() {
		  var geolocation = new AMap.Geolocation({
		    // 是否使用高精度定位，默认：true
		    enableHighAccuracy: true,
		    // 设置定位超时时间，默认：无穷大
		    timeout: 10000
		  })

		  geolocation.getCurrentPosition()
		  AMap.event.addListener(geolocation, 'complete', onComplete)
		  AMap.event.addListener(geolocation, 'error', onError)

		  function onComplete (data) {
		    // data是具体的定位信息
		    $this.$store.dispatch("setLocation",data);
		    $this.$store.dispatch("setAddress",data.formattedAddress);
		    console.log($this.$store.getters.address);
		  }

		  function onError (data) {
		    $this.getcity();
		  }
		})
  	},
  	getcity(){
  		let $this = this;
  		AMap.plugin('AMap.CitySearch', function () {
		  var citySearch = new AMap.CitySearch()
		  citySearch.getLocalCity(function (status, result) {
		    if (status === 'complete' && result.info === 'OK') {
		      // 查询成功，result即为当前所在城市信息
		      AMap.plugin('AMap.Geocoder', function() {
			  var geocoder = new AMap.Geocoder({
			    // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
			    city: result.adcode
			  })
			 
			  var lnglat = result.rectangle.split(';')[1].split(',');

			  geocoder.getAddress(lnglat, function(status, data) {
			    if (status === 'complete' && data.info === 'OK') {
			        // result为对应的地理位置详细信息
			        $this.$store.dispatch("setLocation",data.regeocode);
		   			$this.$store.dispatch("setAddress",data.regeocode.formattedAddress);
		    		console.log(data.regeocode.formattedAddress);
		    		console.log($this.$store.getters.address);
			    }
			  })
			})
		    }
		  })
		})
  	}
  },
  created(){
  	this.getlocation();
  }
}
</script>

<style>
*{
  margin: 0;
  padding: 0;
}
#app{
	width: 100%;
	height: 100%;
}
</style>
