<template>
  <div class="input_group">
    <input :type="type" :value="value" :placeholder="hint" :class="{'notbtn':btnTitle,'iserror':error}" @blur="$emit('input',$event.target.value)">
    <button v-if="btnTitle" @click="$emit('getcode')" class="btn" :disabled="disabled">{{btnTitle}}</button>
    <p v-if="error" class="error">{{error}}</p>
   </div>
</template>

<script>
export default {
  name: 'input_group',
  props: {
    type: String,
    value:String,
    btnTitle:String,
    hint:String,
    disabled:Boolean,
    error:String
  }
}
</script>


<style scoped>
.input_group{
	width: 100%;
	max-width: 600px;
	margin: 0 auto;
	margin-top: 25px;
}
input{
	width: 100%;
	box-sizing: border-box;
	height: 35px;
	border:1px solid #bdbdbd;
	border-radius: 5px;
	padding-left: 1em;
}
.notbtn{
	border-radius: 0px;
	width: 70%;
	border-top-left-radius: 5px;
	border-bottom-left-radius: 5px;
}
button{
	width: 30%;
	border:1px solid #bdbdbd;
	height: 35px;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
}
.error{
	text-align: left;
	color: red;
	margin-left: 1em;
	font-size: 14px;
	margin-top: 5px;
}
.iserror{
	border-color: red;
}
</style>